import React, { Component } from "react";
import { setValue } from "@/redux/simple-word";
import { connect } from "react-redux";
import {
  Form,
  Row,
  Col,
  Input,
  Button,
  Icon,
  message,
  Divider,
  Tag,
  Tooltip,
  Pagination,
  Badge
} from "antd";
import Filter from "co/filter";
import Table from "co/table";
import "./index.less";
import { get, post, deletRequest } from "util/fetch";

import { filters, columns } from "./data";

const routePath = "/api/english/keke";
const routePagePath = "/keke-english";
class index extends Component {
  state = {
    searchButtonLoading: false,
    source: [],
    pagination: {},
    filterResult: {}
  };

  componentDidMount() {
    const pagination = {
      current: this.props.redux.pagination.current,
      pageSize: this.props.redux.pagination.pageSize,
      showSizeChanger: true,
      onShowSizeChange: this.onPaginationSizeChange,
      onChange: this.onPaginationChange,
      // showTotal: (total, range) => `${range[0]}-${range[1]} of ${total} items `
      showTotal: (total, range) => (
        <div className="total">
          {`${range[0]}-${range[1]} of ${total} items`}{" "}
          <span className="blank" />
          <Badge className="yellow" status="processing" text="代表已审核" />
        </div>
      )
    };
    const { filterResult } = this.props.redux;
    this.state.pagination = pagination;
    this.state.filterResult = filterResult;
    this.updateTable();
  }
  onPaginationSizeChange = (current, size) => {
    let { pagination, filterResult } = this.state;
    pagination.pageSize = size;
    this.state.pagination = pagination;
    this.props.setValue({ pagination });

    this.updateTable();
  };
  onPaginationChange = (page, pageSize) => {
    let { pagination, filterResult } = this.state;
    pagination = { ...pagination, current: page, pageSize };
    this.state.pagination = pagination;
    this.props.setValue({ pagination });

    this.updateTable();
  };
  filterResult = values => {
    let { pagination, filterResult } = this.state;
    pagination.current = 1;
    filterResult = values;
    Object.keys(filterResult).forEach(item => {
      if (!filterResult[item]) {
        filterResult[item] = "";
      }
    });
    this.state.filterResult = filterResult;
    this.props.setValue({ pagination, filterResult });
    this.state.pagination = pagination;
    this.updateTable();
  };
  updateTable = () => {
    const { pagination, filterResult } = this.state;
    if (!this.Interceptor()) {
      return;
    }
    this.setState({
      searchButtonLoading: true
    });
    let params = {
      pageNo: pagination.current,
      pageSize: pagination.pageSize,
      ...filterResult
    };
    // params = Object.assign({}, params, values);
    get(routePath, params)
      .then(res => {
        console.log(32, res);
        // return;
        pagination.total = res.count;
        const source = (res.data || []).map((item, index) => {
          const temp = {
            key: (pagination.current - 1) * pagination.pageSize + index + 1,
            ...item
          };
          return temp;
        });
        this.setState({
          searchButtonLoading: false,
          source,
          pagination
        });
        console.log(32, source);
        message.success("查询成功");
      })
      .catch(err => {
        this.setState({
          searchButtonLoading: false
        });
        console.log(32, err);
        message.warn(err.errMsg);
      });
  };
  Interceptor = () => {
    return true;
  };

  createItem = () => {
    this.props.history.push(routePagePath + "/create");
  };
  removeBatchWords = async () => {
    let length = this.state.source.length;
    log(this.state.source);
    message.success("暂不开放", 3);
    return;
    for (let i = 0; i < length; i++) {
      let item = this.state.source[i];
      let id = item.id;
      item = Object.assign({}, item, { collection_name, collection_level });
      // return;
      let result = await deletRequest(`${routePath}/${id}`);
    }
  };
  render() {
    const {
      searchButtonLoading,
      source,
      pagination,
      filterResult
    } = this.state;
    const array = { filters, searchButtonLoading, values: filterResult };
    const tableData = { columns, source, pagination };
    const demo = (
      <div style={{ marginBottom: 10 }}>
        进入演示版：
        <Button
          onClick={() => {
            this.props.history.push("/keke-english/demo");
          }}
        >
          点击
        </Button>
        <div>
          总共：{this.state.pagination.total}
          <Button
            onClick={this.removeBatchWords}
            type="primary"
            style={{ marginLeft: 100 }}
          >
            批量删除
          </Button>
        </div>
      </div>
    );
    return (
      <div className="regular-container">
        <Filter
          array={array}
          filterResult={this.filterResult}
          createItem={this.createItem}
        />
        {demo}
        <Table data={tableData} />
      </div>
    );
  }
}

const mapStateToProps = state => ({
  redux: state.simpleWord
});
const mapDispatchToProps = dispatch => {
  return {
    setValue: value => {
      dispatch(setValue(value));
    }
  };
};

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(index);
